/* styles.css */
.letter {
  display: inline-block;
  transform: translateY(100%);
  opacity: 0;
  transition: transform 1.2s var(--ease-out-circ), opacity 1s linear;
}

.letter.inView {
  transform: translateY(0);
  opacity: 1;
}

