.scroll-icon-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px; /* Высота контейнера */
}

.scroll-icon {
  /* Статичный стиль для мышки */
}

.dot {
  animation: scroll-wheel 1.5s infinite ease-in-out; /* Анимация колесика */
}

/* Анимация колесика */
@keyframes scroll-wheel {
  0% {
    transform: translateY(0); /* Начальная позиция */
    opacity: 1;
  }
  50% {
    transform: translateY(10px); /* Колесико двигается вниз */
    opacity: 0.5;
  }
  100% {
    transform: translateY(0); /* Возвращение в начальную позицию */
    opacity: 1;
  }
}

.scroll-icon-container {
  position: absolute;
  bottom: 12vw;
}

.scroll-icon-container {
  opacity: 0;
  transition: opacity 1s ease-in-out;
  transform: translateX(-50%);
}

.scroll-icon-container.visible {
  opacity: 1;
}

